import Checkbox from '@mui/material/Checkbox/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel/FormControlLabel';
import FormGroup from '@mui/material/FormGroup/FormGroup';
import TextField from '@mui/material/TextField/TextField';
import React, { useState } from 'react';
import DurationControl from 'react-duration-control';

import "react-duration-control/dist/react-duration-control.css";

import GithubCorner from 'react-github-corner';

import './App.css';

function App() {
  const [millis, setMillis] = useState(0);
  const [label, setLabel] = useState("Duration");
  const [pattern, setPattern] = useState("Days {dd} Hours {hh} Minutes {mm} Seconds {ss} Millis {ffff}");
  const [disabled, setDisabled] = useState(false);
  const [hideSpinner, setHideSpinner] = useState(false);
  
  return (
    <div className="app">
      <GithubCorner href="https://github.com/nikkorn/react-duration-control" />
      <div className="page">
        <h1 className="title">react-duration-control</h1>
        <h5 className="title-subtext">A simple React time duration input component that allows the user to define a duration in different units.</h5>
        <hr/>

        <h3 className="subtitle">A Compehensive Example</h3>
        <div className="example-container">
          <DurationControl
            label={label}
            pattern={pattern} 
            value={millis}
            disabled={disabled}
            hideSpinner={hideSpinner}
            onChange={setMillis}
          />
          <TextField style={{ marginTop: "22px" }} label="Millis (Backing Value)" type="number" size="small" value={millis} onChange={(event) => setMillis(parseInt(event.target.value) || 0)} />
          <TextField style={{ marginTop: "12px" }} label="Label" size="small" value={label} onChange={(event) => setLabel(event.target.value)} />
          <TextField style={{ marginTop: "12px" }} label="Pattern" size="small" value={pattern} onChange={(event) => setPattern(event.target.value)} />
          <FormGroup>
            <FormControlLabel control={<Checkbox size="small" checked={disabled} onChange={(event) => setDisabled(event.target.checked)}/>} label="Disabled" />
            <FormControlLabel control={<Checkbox size="small" checked={hideSpinner} onChange={(event) => setHideSpinner(event.target.checked)}/>} label="Hide Spinner" />
          </FormGroup>
        </div>

        <h3 className="subtitle">Install</h3>
        <code className="code-snippet">
          $ npm install react-duration-control
        </code>

        <h3 className="subtitle">Usage</h3>
        <p>A barebones control can be seen in the following code snippet.</p>
        <code className="code-snippet">{`import DurationControl from "react-duration-control";

import "react-duration-control/dist/react-duration-control.css";

const [millis, setMillis] = useState(0);

<DurationControl 
    pattern={"Days {dd} Hours {hh} Minutes {mm} Seconds {ss} Milliseconds {ff}"} 
    value={millis} 
    onChange={setMillis}
/>`}
        </code>
        <p>The backing value for the control is the number of milliseconds represented by the units included in the control pattern and their quantities.</p>

        <h3 className="subtitle">Props</h3>
        <table className="GeneratedTable">
          <thead>
            <tr>
              <th>Prop</th>
              <th>Type</th>
              <th>Default</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{"value"}</td>
              <td>{"number"}</td>
              <td></td>
              <td>{"The value of the control in milliseconds."}</td>
            </tr>
            <tr>
              <td>{"onChange"}</td>
              <td>{"func"}</td>
              <td>{""}</td>
              <td>{"Callback fired when the value in milliseconds is changed. The value is passed as the first and only argument."}</td>
            </tr>
            <tr>
              <td>{"pattern"}</td>
              <td>{"string"}</td>
              <td>{""}</td>
              <td>{"	The pattern used to construct the control contents, defining both the static inline text and inline unit input controls. The pattern will be parsed for any units which are defined using one or more duration unit characters (d=day/h=hour/m=minute/s=second/f=millisecond) wrapped in curly brackets. Any parsed units will be substitued with an inline unit input box which is displayed when the unit value is clicked on in the control. Any unit values will be padded with zeros, the number of which being defined by the number of duration unit characters used. Any part of the pattern that does not match the duration unit syntax will be treated as static inline text."}</td>
            </tr>
            <tr>
              <td>{"label"}</td>
              <td>{"string"}</td>
              <td>{""}</td>
              <td>{"The control label content."}</td>
            </tr>
            <tr>
              <td>{"disabled"}</td>
              <td>{"bool"}</td>
              <td>{"false"}</td>
              <td>{"If true, the control will be disabled."}</td>
            </tr>
            <tr>
              <td>{"hideSpinner"}</td>
              <td>{"bool"}</td>
              <td>{"false"}</td>
              <td>{"If true, the up and down spinner buttons will not be shown."}</td>
            </tr>
          </tbody>
        </table>
        <br/>
      </div>
    </div>
  );
}

export default App;
